import { Field } from 'mint-ui';
import { MessageBox } from 'mint-ui';
import cTitle from '../../components/title';
export default {
    data() {
        return {
            form: {
                num: 13504842476
            },
            //验证码
            code: '',
            action_name: '绑定',
            CodeStr: "获取验证码",
            timer: 30,
            stop: false,   //默认是停止的，但界面加载之后会变成false
            Interval: null,  //setInterval的对象
            lock: false      //数秒期间不能点击按钮

        }
    },
    methods:
    {


        update() {

            this.timer--;
            if (this.timer == 0) {
                //停止数秒，重新获取
                clearInterval(this.Interval);
                this.timer = 30;
                this.CodeStr = '获取验证码';
                this.lock = false;


            } else {
                this.CodeStr = String(-this.timer);

            }


        },
        //获取验证码
        VerificationCode() {
            //如果是false就开始倒计时，如果是true就停止倒计时
            if (this.lock == false) {

                var pattern = /0?(13|14|15|18)[0-9]{9}/;

                if (!pattern.test(this.form.num)) {
                    MessageBox.alert("请输入正确的手机号", '提示');
                    return;
                } else {
                    this.Interval = setInterval(this.update, 1000);
                    this.lock = true;
                    //发送获取验证码的请求
                    $http.get('member.register.sendCode', { mobile: this.form.num }).then(function (response) {

                        console.log(response.data)
                    }, function (response) {
                        // error callback
                    });

                }

            }
            else {
                return;
            }


        },
        //提交修改手机的请求
        alterAction() {

            var that = this;
            //发送获取验证码的请求
            if (this.form.num.length == 0) {
                MessageBox('手机号码不能为空', '提示');
            } else if (this.code.length == 0) {
                MessageBox('请填写验证码', '提示');

            }
            else {
                $http.get('member.register.sendCode', { mobile: this.form.num }, '正在修改手机..').then(function (response) {

                    console.log(response.data)
                    if (response.result == 1) {
                        MessageBox.alert(response.msg, "提示");
                        that.$router.go(-1);
                        that.$emit('editmobile',that.form.num);
                    } else {
                        MessageBox.alert(response.msg, "提示");
                    }
                }, function (response) {
                    // error callback
                });

            }




        }


    }, activated() {

        clearInterval(this.Interval);
        this.timer = 30;
        this.CodeStr = '获取验证码';
        this.lock = false;
        this.form.num = this.$route.params.num;
        if (this.$route.params.num) {
            this.action_name = '绑定手机';
        } else {
            this.action_name = '修改手机';
        }



    },
    components: { cTitle }
}


